.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  min-height: 66px;
}

.header-button {
  margin-top: 23px;
  margin-right: 20px;
  border: none;
  background: url("../img/icon-close.svg") no-repeat;
  width: 24px;
  height: 18px;
}

.header-button-close {
  border: none;
  background: url("../img/icon-mobile-menu.svg") no-repeat;
  width: 24px;
  height: 18px;
}


.header__logo-image:hover,
.header__logo-text:hover {
  opacity: 0.8;
}

.header__logo-image:active,
.header__logo-text:active {
  opacity: 0.6;
}

.header__logo-image {
  margin-top: 11px;
  margin-left: 20px;
}

.header__logo-text {
  margin-top: 24px;
  padding-right: 9px;
}

@media (min-width: $tablet-width) {
  .header__wrapper {
    @include tablet-mix;
    @include container-tablet;
    display: flex;
    flex-wrap: wrap;
  }

  .header__logo-image {
    margin-top: 24px;
    margin-left: 0;
    padding: 0;
  }

  .header-button {
    display: none;
    padding-right: 20px;
  }

  .header__logo-text {
    display: none;
  }

  .header__logo-image img {
    width: 173px;
    height: 58px;
  }
}


@media (min-width: $desktop-width) {
  .header {
    padding-bottom: 54px;
  }

  .header__wrapper {
    @include desktop-mix;
    @include container-desktop;
  }

  .header-index {
    background: url("../img/index-background-desktop@1x.jpg") top right no-repeat, linear-gradient(to right, $base-color 50%, $green-gradient 50%);
  }

  .header__logo-image {
    margin-top: 55px;
  }

  .header__logo-image img {
    width: 202px;
    height: 59px;
  }
}
